var ImgView = function(imgs){
  this.render(imgs);
}

ImgView.prototype = {
  render: function(imgs){

    // console.log(imgs)
    // console.log(imgs[0]._id)
    // var imgTag = document.createElement('img');
    // var div = document.querySelector('#imgs');
    // imgTag.src = "data:image/gif;base64,"
    // div.appendChild(imgTag)

    imgs.forEach( function(img){

      var imgTag = document.createElement('img');
      var div = document.querySelector('#imgs');

      var id = img._id
      console.log(id)
      imgTag.innerHtml(id)
      var src = "data:image/gif;base64" + id
      imgTag.src = src
      console.log(src)
      div.appendChild(imgTag)
    })
  }
}

module.exports = ImgView;



